#PoliciesList {
    font-weight: 400;
}

.policy-li {
    padding: 10px 10px 10px 15px;
}

.policy-li:hover {
    background-color: #fafbfd;
    border-color: #fafbfd;
    box-shadow: inset 3px 0 0 #0070d2;
    cursor: pointer;
}

#PoliciesList div h1 {
    font-size: 14px;
    font-weight: 400;
    display: block;
    width:222px;
}

.policy-selected {
    background-color: #fafbfd;
    border-color: #fafbfd;
    box-shadow: inset 3px 0 0 #0070d2;
    cursor: pointer;
}

#PoliciesList div svg {
    fill: #0071d2!important;
    padding: 4px;
    top: -20px;
    right: -9px;
    position: relative;
    height: 14px;
    width: 16px;
    border-radius: 4px;
    border: 1px solid #d8dde6;
    float: right;
    background: #fff;
    cursor: pointer;
    display: none;
}

.policy-li:hover svg {
    display: initial !important;
}

.policy-li:hover svg:hover {
    box-shadow: 0 2px 2px rgba(0,0,0,0.1);
}