#ob-toast {
    position: absolute;
    z-index: 10;
    font-size: 17px;
    color: #fff;
    background: #04844b;
    border-radius: 4px;
    width: calc(70% - 40px);
    margin-left: 15%;
    top: 22px;
    text-align: center;
    padding: 13px 0;
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.19);
    display: none;
}