.skills-container {
    background-color: #fff;
    border: 1px solid #d8dde6;
    border-radius: 4px;
    width: 65%;
    font-size: 14px;
}

@-webkit-keyframes bounce {

    0%, 20%, 50%, 80%, 100% {
        -webkit-transform: translateY(0);
    }


    40% {
        -webkit-transform: translateY(-16px);
    }


    60% {
        -webkit-transform: translateY(-7px);
    }
}

@-moz-keyframes bounce {

    0%, 20%, 50%, 80%, 100% {
        -moz-transform: translateY(0);
    }


    40% {
        -moz-transform: translateY(-16px);
    }



    60% {
        -moz-transform: translateY(-7px);
    }
}

@-o-keyframes bounce {

    0%, 20%, 50%, 80%, 100% {
        -o-transform: translateY(0);
    }


    40% {
        -o-transform: translateY(-16px);
    }


    60% {
        -o-transform: translateY(-7px);
    }
}

@keyframes bounce {

    0%, 20%, 50%, 80%, 100% {
        transform: translateY(0);
    }


    40% {
        transform: translateY(-16px);
    }


    60% {
        transform: translateY(-7px);
    }
}

.bounce {
    animation-name: bounce;
}

.clearfix:before, .clearfix:after {
    display: block;
    content: "";
    line-height: 0;
    clear: both;
}

.taggle_list {
    float: left;
    width: 100%;
    list-style: none;
    margin: 0;
    padding: 0;
}

.taggle_input {
    border: none;
    outline: none;
    font-size: 14px;
}

.taggle_list li {
    float: left;
    display: inline-block;
    white-space: nowrap;
    font-weight: 500;
    margin-bottom: 5px;
    margin-left:2px;
}

.taggle_list .taggle {
    cursor: pointer;
    transition: all .3s;
    animation-duration: 1s;
    animation-fill-mode: both;
}

.taggle_list .taggle_hot {
    background: #cac8c4;
}

.taggle_list .taggle .close {
    font-size: 1.1rem;
    position: absolute;
    top: 5px;
    right: 3px;
    text-decoration: none;
    padding-left: 2px;
    padding-top: 3px;
    line-height: 0.5;
    color: #ccc;
    color: rgba(0, 0, 0, 0.2);
    padding-bottom: 4px;
    display: none;
    border: 0;
    background: none;
    cursor: pointer;
}

.taggle_list .taggle:hover {
    padding: 5px;
    padding-right: 22px;
    background: #f4f6f9;
    transition: all .3s;
}

.taggle_list .taggle:hover > .close {
    display: block;
}

.taggle_list .taggle .close:hover {
    color: #555;
}

.taggle_placeholder {
    position: absolute;
    color: #CCC;
    top: 12px;
    left: 8px;
    transition: opacity, .25s;
    user-select: none;
}

.taggle_input {
    padding: 3px;
    padding-left:0;
    float: left;
    margin-top: 0px;
    background: none;
    width: 100%;
    max-width: 100%;
}

.taggle_sizer {
    padding: 0;
    margin: 0;
    position: absolute;
    top: -500px;
    z-index: -1;
    visibility: hidden;
}

/*container styles*/
textarea.input,
.textarea.input {
    border: 0;
    min-height: 60px;
    /*padding: 8px;*/
    border-radius: 3px;
    color: #555;
    transition: all .25s;
    cursor: text;
    /*margin-bottom: 10px;*/
    position: relative;
    padding: 10px;
}

.textarea.input:focus,
.textarea.input.active,
textarea.input:focus,
textarea.input.active {
    background: #fff;
    transition: all .25s;
}

.textarea.input,
textarea.input {
    height: auto;
}


.textarea.tags {
    position: relative;
}

.textarea.tags * {
    box-sizing: content-box;
}


.placeholder_input {
    position: relative;
}

.placeholder_input span {
    position: absolute;
    color: #AAA;
    top: 50%;
    margin-top: -11px;
    left: 10px;
}

.placeholder_input input {
    width: 120px;
}

/*jQuery autocomplete example*/


.ui-autocomplete {
   position: absolute;
   top: 0;
   left: 0;
}

.ui-menu {
    list-style: none;
    padding: 2px;
    margin: 0;
    display: block;
    outline: none;
}


.ui-menu .ui-menu-item {
    margin: 0;
    padding: 0;
    width: 100%;
}

.ui-menu .ui-menu-item a {
    text-decoration: none;
    display: block;
    padding: 2px .4em;
    line-height: 1.5;
    min-height: 0;
    font-weight: normal;
    color: #eee;
    cursor: pointer;
}

.ui-menu .ui-menu-item a:hover {
    color: #292929;
    background: #fafbfd;
    cursor: pointer !important;
 }


.ui-state-hover,
.ui-widget-content .ui-state-hover,
.ui-widget-header .ui-state-hover,
.ui-state-focus,
.ui-widget-content .ui-state-focus,
.ui-widget-header .ui-state-focus {
    color: #292929;
    background: #fafbfd !important;
}

.ui-state-hover a,
.ui-state-hover a:hover,
.ui-state-hover a:link,
.ui-state-hover a:visited {
    color: #fff;
}

.ui-state-active,
.ui-widget-content .ui-state-active,
.ui-widget-header .ui-state-active {
    border: 1px solid #aaaaaa;
    background: #ffffff;
    font-weight: normal;
    color: #212121;
}

.ui-helper-hidden {
    display: none;
}

.ui-helper-hidden-accessible {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

.autocomplete {
    max-height: 200px;
    overflow: scroll;
    position: absolute;
    top: 66px;
    background: white;
    width: 99.5%;
    left: 0.25%;
