}

.autocomplete ul li {
    display: block;
    padding: 6px 8px;
}

.autocomplete ul li.selected, .autocomplete ul li:hover {
    background: #ff6633;
    color: #fff;
    cursor: pointer;
}

.ui-autocomplete {
    max-height: 200px;
    overflow: auto;
    width: 99%!important;
