.page-header {
    padding-bottom: 10px;
    border-bottom: 1px solid #d8dde6;
    margin-bottom: 30px;
}

.page-header h1 {
    font-weight: 300;
    margin: 10px 0 20px;
    display: block;
    font-size: 1.75rem;
    line-height: 1.25;
}

.page-header p {
    font-size: 15px;
    line-height: 24px;
    color: #767676 !important;
    font-weight: lighter;
}

.page-header .icon-on-header {
    fill: #FFF!important;
    padding: 1px;
    top: 7px;
    position: relative;
    height: 30px;
    width: 30px;
    margin-right: 13px;
    border-radius: 4px;
}

.show-more-less {
    margin-left: 5px;
    color: #0071d2;
    cursor: pointer;
}