.add-new-territory {
    margin-bottom: 15px;
    display: inline-block;
}

.add-new-territory input {
    background-color: #fff;
    border: 1px solid #d8dde6;
    border-radius: 4px;
    width: 220px;
    padding: 7px 8px 8px 8px;
    font-size: 14px;
    margin-right: 5px;
    outline: 0;
}


.add-new-territory button {
    padding: 2px 10px 1px;
    font-size: 13px;
    top: -1px;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    font-weight: 400;
}

.add-new-disabled,
.add-new-disabled:hover,
.add-new-disabled:active {
    color: #bbc3d2;
    cursor: not-allowed;
}

.add-new-saving {
    display: inline-block;
    font-size: 14px;
    color: #9eaab5;
    margin-top: 3px;
}

.add-new-saving img {
    width: 20px;
    height: 20px;
    position: relative;
    top: 4px;
    margin-right: 6px;
}