#utt-table-container {
    height: 100%;
}


.utt-table-head {
    font-size: .75rem;
    line-height: 1.25;
    color: #706e6b;
    text-transform: uppercase;
    letter-spacing: .0625rem;
    box-shadow: 0 -1px 0 #dddbda;
    background: #fafaf9;
    border-bottom: 1px solid #dddbda;
    padding: 10px;
}

.utt-table-head-cell {
    display: inline-block;
    width: 25%;
    user-select: none;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    position: relative;
    padding: 2px 0;
    cursor: pointer;
}

.utt-territories-cell {
    width: calc(75% - 80px)!important;
    position: relative;
}

.utt-table-row {
    padding: 10px;
    border-bottom: 1px solid #dddbda;
}

.utt-table-cell {
    display: inline-block;
    position: relative;
    width: 25%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.utt-territories-cell-license {
    width: 80px !important;
}

.utt-territories-cell > span {
    display: none;
    position: absolute;
    right: 10px;
    background: #fff;
    color: #0071d2;
    border-radius: 3px;
    border: 1px solid #d8dde6;
    padding: 0px 4px;
    margin-top: 0px;
    text-transform: uppercase;
    letter-spacing: 1px;
    cursor: pointer;
    font-size: 10px;
}

.utt-table-row:hover .utt-territories-cell > span {
    display: initial;
}

.utt-territory-names-table {
    display: inline-block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: calc(100% - 60px);
}

.cut-arrow {
    width: 12px;
    height: 12px;
    fill: #706e6b;
    position: absolute;
    margin: 2px 0 0 2px;
}


.utt-table-row a svg {
    width: 13px;
    height: 13px;
    float: right;
    margin: 3px 10px 0 0;
    fill: #8e8e8e;
    cursor: pointer;
    position: absolute;
    top: -2px;
    right: 0;
    display: none;
}

.utt-table-row:hover a svg {
    display: initial!important;
}

.utt-table-row a svg:hover {
    fill: #0071d2;
}