.worktype-single {
    font-size: 14px;
    height: 25px;
    border-left: 3px solid #fff;
    padding: 12px 15px;
    line-height: 25px;
    cursor: pointer;
    border-bottom: 1px solid #eee;
    transition: border .2s cubic-bezier(0.55, 0.09, 0.68, 0.53);
}

.worktype-single:hover, .worktype-selected {
    background-color: #fafbfd;
    border-color: #fafbfd;
    border-left: 3px solid #0070d2;
}

.skill-reqs-container {
    display: inline-block;
    float: right;
    width: calc(50% - 20px);
}