.leftPanelBody {
    width: 300px;
    height: 100%;
    background: #fff;
    margin-right: 10px;
    border-right: 1px solid #d8dde6;
    position: absolute;
}

.setupProgress {
    padding: 20px 10px;
    margin-bottom: 20px;
    border-bottom: 1px solid #d8dde6;
}

.progressBar {
    background: #f4f6f9;
    margin: 10px 0;
}

.actualProgress {
    padding: 5px 10px;
    background: #5eb4ff;
    width: 45%;
    text-align: right;
    color: #fff;
}