.oh-form {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.oh-form .ohs-label {
    width: 35%;
    font-size: 14px;
}

.oh-form .ohs-input {
    background-color: #fff;
    border: 1px solid #d8dde6;
    border-radius: 4px;
    width: 65%;
    font-size: 14px;
    outline: 0;
}

.oh-form .ohs-input select {
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    padding: 10px;
    background: transparent;
    border: 0;
    width: 100%;
    z-index: 10;
    position: relative;
    outline: 0;
    font-size: 14px;
}


.oh-form .ohs-input::before {
    content: '';
    position: absolute;
    background: transparent;
    padding: 3px;
    border-bottom: 1px solid #c3c8d0;
    border-right: 1px solid #c3c8d0;
    transform: rotate(45deg);
    right: 14px;
    margin-top: 13px;
}