.operating-hours-creator select {
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    padding: 5px 8px;
    background: transparent;
    border: 0;
    width: 100%;
    z-index: 10;
    position: relative;
    outline: 0;
    font-size: 14px;
}

.time-container-thingy {
    background-color: #fff;
    border: 1px solid #d8dde6;
    border-radius: 4px;
    width: 55px;
    font-size: 14px;
    outline: 0;
    margin: 20px 3px 20px 7px;
    display: inline-block;
}

.time-container-thingy::before {
    content: '';
    position: absolute;
    background: transparent;
    padding: 3px;
    border-bottom: 1px solid #c3c8d0;
    border-right: 1px solid #c3c8d0;
    transform: rotate(45deg);
    margin-left: 38px;
    margin-top: 9px;
}


.minutes-select {
    margin-left: 4px;
}