.objectives-h2 {
    font-weight: 400;
    margin-top: 40px;
    font-size: 20px;
    display: block;
}

.objectives-p {
    line-height: 24px;
}

#ObjectiveEditorContainer {
    margin-bottom: 40px;
}

.slider-container {
    padding: 15px 10px;
    width: 340px;
    margin: 10px 40px 10px 0;
    display: inline-block;
    border-radius: 4px;
    text-align: left;
}

.slider-container:hover {
    background: #f8fafd;
}

.dhtmlxslider_material {
    margin-top: 10px;
}

.dhtmlxslider_material .dhxsl_track {
    background: #d8dde6;
    height: 1px;
}

.dhtmlxslider_material .dhxsl_runner {
    background: #0071d2;
    border-radius: 3px;
    width: 7px;
    height: 16px;
}

.slider-value {
    color: #0071d2;
    font-size: 16px;
    border-radius: 3px;
    float: right;
    margin-top: -1px;
}

.objective-sliders-container {
    text-align: center;
    overflow: hidden;
}

.slider-val {
    font-size: 10px;
    position: relative;
    top: 10px;
}

.slider-val-max {
    float: right;
}

#pie-chart-container {
    float: left;
}

#objectives-pie {
    width: 680px;
    height: 500px;
}

