.rules-selector-select {
    background-color: #fff;
    border: 1px solid #d8dde6;
    border-radius: 4px;
    width: 100%;
    font-size: 14px;
    outline: 0;
    height: 250px;
}
.rules-selector-select option {
    padding: 8px;
    font-size: 13px;
}

.rules-selector-select option:checked{
    background: #015ba7;
    color: #fff;
}

.rules-h2 {
    font-weight: 400;
    margin-top: 40px;
    font-size: 20px;
    display: block;
}

.rules-p {
    line-height: 24px;
}

.rules-select-header {
    margin: 15px 0 8px 0;
    color: #767676;
}

.rules-select-container {
    float: left;
    width: calc(50% - 30px);
}

.rules-buttons-container {
    float: left;
    width: 40px;
    padding: 41px 10px 0 10px;
    text-align: center;
}

.rules-buttons-container img {
    width: 24px;
}

.rules-move-button {
    padding: 5px;
    border-radius: 3px;
    border: 1px solid #d8dde6;
    margin-bottom: 10px;
    cursor: pointer;
    user-select: none;
}

.rules-move-button:hover {
    background: #fbfbfb;
}

.rules-move-button:active {
    background: #f4f6f9;
}

#RulesSelectionContainer {
    display: inline-block;
    width: 100%;
}

.ob-noars {
    color: #fb6464;
    margin-top: 12px;
}

.lightbox-rules-explain {
    max-height: 410px;
    overflow-y: auto;
    padding: 10px;
}

.lightbox-rules-explain ul {
    padding-left: 5px;
}

.lightbox-rules-explain li {
    margin-bottom: 11px;
}

.lightbox-rules-explain h1 {
    font-size: 16px;
}

.label-link {
    color: #0070d2 !important;
    cursor: pointer;
}