.operating-hours-creator {

}

.operating-hours-creator input[type=text] {
    background-color: #fff;
    border: 1px solid #d8dde6;
    border-radius: 4px;
    width: 400px;
    padding: 7px 9px;
    font-size: 13px;
    margin: 0 0 20px 10px;

}

.ohc-days-inner {
    margin-bottom: 10px;
}

.time-container {
    display: inline-block;
}

.operating-hours-creator h1 {
    font-size: 24px;
    font-weight: 400;
    display: block;
    margin: 5px 0 15px 0;
}

.operating-hours-creator h2 {
    font-size: 15px;
    line-height: 24px;
    color: #767676;
    font-weight: lighter;
    margin-bottom: 20px;
    display: block;
    padding-bottom: 20px;
    border-bottom: 1px solid #d8dde6;
}

.ohc-day-selected {
    color: #0070d2;
    border-bottom: 1px dotted #0070d2;
}

.ohc-day-not-selected {
    color: #767676;
}

.operating-hours-creator div > span {
    margin-right: 5px;
    cursor: pointer;
}

.operating-hours-creator #ohc-days > span::after {
    content: ',';
}

.operating-hours-creator #ohc-days > span:last-child::after {
    content: '';
}



.ohc-button-location {
    position: absolute !important;
    bottom: 15px;
    right: 20px;
}

.ohc-select-tz {
    width: 356px;
    margin-top: 0;
    padding-right: 20px;
}

.ohc-select-tz::before {
    right: 227px;
    margin-top: 11px;
    margin-left: 0;
}

.ohc-select-tz select {
    padding: 8px 8px 7px 8px;
}

.select-slots {
    -webkit-appearance: none;
    padding: 5px 8px;
    background: transparent;
    border: 0;
    width: 100%;
    z-index: 10;
    position: relative;
    outline: 0;
    font-size: 14px;
}

.slots-creator-select {
    margin: 0 7px 0 9px;
}

.ohc-saving-spinner {
    width: 24px;
    height: 24px;
    margin: 0 5px -7px 0;
}

.ohc-saving-container {
    position: absolute;
    bottom: 20px;
    left: 10px;
}

.ohc-explain-ab {
    color: #80858a; font-style: italic;
}

#ohc-content {
    position: absolute;
    overflow: auto;
    max-height: 327px;
}