.policy-link-layout {
    text-align: right;
    color: #0070d2;
    font-size: 13px;
    text-decoration: none;
    float: right;
    margin-top: 5px;
    margin-right: -100px;
    cursor: pointer;
}
.rd-comments-ul {
    padding-left: 0;
}

.rd-comments-ul li {
    margin-bottom: 10px;
}
.form-input select {
    border: 1px solid #d8dde6;
    border-radius: 4px;
    padding: 10px;
    font-size: 14px;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    padding: 10px;
    background: transparent;
    width: 100%;
    z-index: 10;
    position: relative;
    outline: 0;
    font-size: 14px;
}

.form-select::before {
    content: '';
    position: absolute;
    background: transparent;
    padding: 3px;
    border-bottom: 1px solid #c3c8d0;
    border-right: 1px solid #c3c8d0;
    transform: rotate(45deg);
    right: 14px;
    margin-top: 13px;
}

.inday-disabled {
    margin-bottom: 8px;
    display: block;
    font-weight: bold;
    font-size: 15px;
}

/* checkbox */
.checkbox_toggle [type="checkbox"] {
    appearance:none;
    display: block;
    position: relative;
    width: 3rem;
    height: 24px;
    border: 1px solid #b0adab;
    padding: 0.125rem;
    background-color: #b0adab;
    border-radius: 15rem;
    transition: background-color 0.2s cubic-bezier(0.75, 0, 0.08, 1);
}

.checkbox_toggle [type="checkbox"]:hover{
    cursor: pointer;
    background-color: #969492; 
}

.checkbox_toggle [type="checkbox"]:disabled {
    cursor: not-allowed;
    background-color: #ccc; 
}

.checkbox_toggle [type="checkbox"]:disabled:after {
    cursor: not-allowed;
    background-color: #dfdfdf; 
}

.checkbox_toggle [type="checkbox"]:after {
    content: '';
    position: absolute;
    top: 1px;
    left: 1px;
    width: 1.25rem;
    height: 1.25rem;
    background-color: white;
    border-radius: 15rem; }


.checkbox_toggle [type="checkbox"] {
    font-size: 0.625rem;
    color: #706e6b; }


.checkbox_toggle [type="checkbox"]:checked {
    border-color: #0070d2;
    background-color: #0070d2;
}


.checkbox_toggle [type="checkbox"]:checked:before {
    content: '';
    position: absolute;
    top: 1px;
    right: 1px;
    width: 1.25rem;
    height: 1.25rem;
    background-color: white;
    border-radius: 15rem;
    transition: transform 0.2s cubic-bezier(0.75, 0, 0.08, 1);
}


.checkbox_toggle [type="checkbox"]:checked:after {
    content: ' ';
    position: absolute;
    top: 0.25rem;
    left: 0.6rem;
    height: 0.7rem;
    width: 0.45rem;
    border-bottom: 2px solid white;
    border-right: 2px solid white;
    border-radius: 0;
    background-color: transparent;
    transform: rotate(45deg); }
