.menu-link {
    display: block;
    font-size: 14px;
    text-decoration: none;
    padding: .875rem 1rem .8125rem;
    line-height: 1.25;
    color: #3c3c3c;
}

.menu-link:hover {
    box-shadow: inset 3px 0 0 #0070d2;
    text-decoration: none;
    color: #0070d2;
    background: #fafbfd;
}

.obnav-active {
    background: #f4f6f9;
    font-weight: bold;
    box-shadow: inset 3px 0 0 #0070d2;
}