.due-date-calculated {
    color: #0070d2;
    padding: 5px 0px 0 5px;
    font-size: 12px;
    position: absolute;
}

.form-header h2 {
    font-weight: 400;
    margin: 0 0 15px;
    font-size: 24px;
    display: inline-block;
    width: calc(100% - 200px);
}

.form-header span {
    text-align: right;
    color: #0070d2;
    font-size: 13px;
    text-decoration: none;
    float: right;
    margin-top: 5px;
    cursor: pointer;
    width: 190px;
    margin-left: 10px;
}

/* checkbox */
.checkbox_toggle [type="checkbox"] {
    appearance:none;
    display: block;
    position: relative;
    width: 3rem;
    height: 24px;
    border: 1px solid #b0adab;
    padding: 0.125rem;
    background-color: #b0adab;
    border-radius: 15rem;
    transition: background-color 0.2s cubic-bezier(0.75, 0, 0.08, 1);
}

.checkbox_toggle [type="checkbox"]:hover {
    cursor: pointer;
    background-color: #969492; }


.checkbox_toggle [type="checkbox"]:after {
    content: '';
    position: absolute;
    top: 1px;
    left: 1px;
    width: 1.25rem;
    height: 1.25rem;
    background-color: white;
    border-radius: 15rem; }


.checkbox_toggle [type="checkbox"] {
    font-size: 0.625rem;
    color: #706e6b; }


.checkbox_toggle [type="checkbox"]:checked {
    border-color: #0070d2;
    background-color: #0070d2;
}


.checkbox_toggle [type="checkbox"]:checked:before {
    content: '';
    position: absolute;
    top: 1px;
    right: 1px;
    width: 1.25rem;
    height: 1.25rem;
    background-color: white;
    border-radius: 15rem;
    transition: transform 0.2s cubic-bezier(0.75, 0, 0.08, 1); }


.checkbox_toggle [type="checkbox"]:checked:after {
    content: ' ';
    position: absolute;
    top: 0.25rem;
    left: 0.6rem;
    height: 0.7rem;
    width: 0.45rem;
    border-bottom: 2px solid white;
    border-right: 2px solid white;
    border-radius: 0;
    background-color: transparent;
    transform: rotate(45deg); }


.side-form {
    width: calc(55% - 50px);
}

