.lightbox-black {
    position: fixed;
    position: -ms-page;
    width: 100%;
    height: 100%;
    background: rgba(126,140,153,.8);
    top: 0;
    left: 0;
    z-index: 1000;
    padding-top: 30px;
}

.lightbox-container {
    background: #fff;
    margin: 55px auto 0;
    padding: 15px;
    position: relative;
    border-radius: 8px;
    color: #16325c;
    font-size: 14px;
    overflow: auto;
}

.lightbox-close-icon {
    cursor: pointer;
    fill: #fff;
    float: right;
    width: 30px;
    height: 30px;
}

.lightbox-close-container {
    margin: 0 auto;
    margin-top: 13px;
}