.territory-form {
    position: absolute;
    right: 20px;
    width: calc(50% - 50px);
    padding-left: 20px;
    border-left: 1px solid #d8dde6;
    font-size: 14px;
    height: calc(100% - 200px);
}

.territory-form .select-a-territory {
    font-size: 16px;
    line-height: 30px;
    color: #767676;
    font-weight: lighter;
    padding: 20px 5px;
}

.territory-form .form-row-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.territory-form .label {
    width: 35%;
}

.territory-form .form-input {
    width: 65%;
}

.territory-form .form-input input[type="text"]{
    background-color: #fff;
    border: 1px solid #d8dde6;
    border-radius: 4px;
    width: calc(100% - 22px);
    padding: 10px;
    font-size: 14px;
}

.territory-form.form-arrow::before {
    content: "";
    background: #fff;
    position: absolute;
    margin-left: -31px;
    margin-top: 30px;
    width: 20px;
    height: 20px;
    transform: rotate(45deg);
    border-left: 1px solid #d8dde6;
    border-bottom: 1px solid #d8dde6;
}

.territory-form .create-operating-hours {
    text-align: right;
    color: #0070d2;
    font-size: 13px;
}

.territory-form .create-operating-hours div {
    display: inline-block;
    cursor: pointer;
}


.form-territory-address {
    display: inline-block;
    font-weight: 500;
    margin: 35px 0 15px;
    font-size: 18px;
}


.open-external-link {
    text-align: right;
    color: #0070d2;
    font-size: 13px;
    text-decoration: none;
    margin-right: 18px;
}

.territory-form-header h2 {
    font-weight: 400;
    margin: 0 0 15px;
    font-size: 24px;
    display: inline-block;
    width: calc(100% - 200px);
}

.territory-form-header span {
    text-align: right;
    color: #0070d2;
    font-size: 13px;
    text-decoration: none;
    float: right;
    margin-top: 5px;
    cursor: pointer;
    width: 190px;
    margin-left: 10px;
}


.locate-in-gmap {
    margin-top: 36px;
    float: right;
    margin-right: 0;
}

.territory-address-mafrid {
    border-top: 1px solid #d8dde6;
    margin-top: 35px;
}
