.object-counter-container {
    display: inline-block;
    width: 200px;
    border: 1px solid #d8dde6;
    margin: 10px;
    border-radius: 4px;
    text-align: center;
}

.number-counter {
    font-size: 30px;
    font-weight: 300;
    padding: 0 20px;
}

.counter-object-name {
    font-size: 14px;
    padding: 5px 5px 15px;
}

.object-counter-container svg {
    padding: 0;
    height: 64px;
    width: 64px;
    margin-top: 10px;
    border-radius: 4px;
}