.bulk-ter-actions {
    position: absolute;
    right: 20px;
    width: calc(50% - 50px);
    padding-left: 20px;
    border-left: 1px solid #d8dde6;
    font-size: 14px;
    height: calc(100% - 200px);
}

.bulk-ter-actions h2 {
    display: inline-block;
    font-weight: 500;
    margin-bottom: 24px;
    font-size: 18px;
}

.open-external-link {
    text-align: right;
    color: #0070d2;
    font-size: 13px;
    text-decoration: none;
    margin-right: 18px;
}

.bulk-ter-actions .create-operating-hours {
    text-align: right;
    color: #0070d2;
    font-size: 13px;
}

.bulk-ter-actions .create-operating-hours div {
    display: inline-block;
    cursor: pointer;
}