#rba-bulk {
    top: -2px;
}

.rba-label {
    width: 35%;
    display: inline-block;
}

.rba-input {
    width: 65%;
    display: inline-block;
}

.rba-input input[type=text] {
    width: calc(100% - 23px);
    border-radius: 3px;
    border: 1px solid #d8dde6;
    padding: 9px 10px;
    outline: 0;
    font-size: 14px;
}


#rba-oh-home {
    width: 450px;
    display: inline-block;
}

#rba-oh-home .oh-form {
    margin-bottom: 15px !important;
}

#rba-oh-home .ohs-input::before {
    right: 428px !important;
}

.rba-field {
    margin-bottom: 15px;
}

.rba-title {
    font-size: 18px;
    padding: 10px 0 20px 0;
    border-bottom: 1px solid #d8dde6;
    margin-bottom: 25px;
}

.rba-skills-container h1 {
    font-weight: normal;
    font-size: 16px;
    margin-top: 5px;
    display: block;
}

.rba-skills-container select {
    appearance: none;
    position: relative;
    background: #fff;
    border: 0;
    font-size: 16px;
    margin-right: 5px;
    color: #0071d2;
    border-bottom: 1px dashed #0071d2;
    border-radius: 0;
    outline: 0;
    cursor: pointer;
}

.rba-skills {
    border: 1px solid #d8dde6;
    padding: 10px;
    height: 310px;
    overflow-y: overlay;
    border-radius: 3px;
    margin-top: 15px;
    margin-bottom: 13px;
}

.rba-skill-container {
    display: inline-block;
    width: 160px;
    margin-right: 4px;
    margin-bottom: 10px;
}


.rba-skill-container label {
    width: calc(100% - 25px);
    display: inline-block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    position: relative;
    top: 3px;
    left: 3px;
}

#rba-button {
    float: right;
}


#rba-oh-home h1 {
    font-weight: 400;
    font-size: 16px;
    margin-bottom: 16px;
    display: block;
}

.rba-saving {
    float: left;
}

.rba-saving img {
    width: 24px;
    height: 24px;
    position: relative;
    top: 6px;
    margin-right: 8px;
}

.rba-skills-container input[type=search] {
    position: absolute;
    right: 15px;
    top: 18px;
    width: 250px;
    border-radius: 3px;
    border: 1px solid #d8dde6;
    font-size: 13px;
    padding: 5px 10px;
}