.day-container-slots {
    border: 1px solid #eee;
    border-radius: 4px;
    display: inline-block;
    width: 155px;
    margin-right: 10px;
    font-size: 14px;
    float: left;
    margin-top: 20px;
}

.day-name-container-slots {
    padding: 8px 10px;
    border-bottom: 1px solid #eee;
    margin-bottom: 10px;
    background: #f4f6f9;
}

.single-day-slot {
    padding: 10px;
}

.single-day-slot:last-child {
    border-bottom: 0;
}

#SlotsViewContainer {
    overflow: hidden;
    margin-top: 30px;
}

.slots-prev-exp {
    font-size: 16px;
}

#TimezoneWarning {
    margin-top: 20px;
    font-size: 14px;
    font-style: italic;
    color: #767676;
}