#rd-search-bar {
    display: inline;
}

#rd-search-bar input {
    background: #fff;
    border: 1px solid #d8dde6;
    border-radius: 4px;
    width: 260px;
    padding: 8px 32px 7px 11px;
    font-size: 13px;
    outline: 0;
}

#rd-search-bar svg {
    width: 14px;
    height: 14px;
    position: relative;
    left: -25px;
    top: 2px;
    fill: #706e6b;
}

