#contentWrapper {
    position: absolute;
    height: calc(100% - 165px);
    width: calc(100% - 20px);
}

body.sfdcBody {
    padding: 0 !important;
}

#OnboardingRoot {
    position: absolute;
    width: 100%;
    height: 100%;
}

#OnboardingRoot :not(i) {
    font-family: 'Salesforce Sans', Arial, sans-serif;
}

.bodyDiv {
    background: #f4f6f9;
}

.bodyDiv :not(i) {
    font-family: 'Salesforce Sans', Arial, sans-serif;
}

.noSidebarCell {
    padding: 0;
}

h1 {
    font-family: 'Salesforce Sans', Arial, sans-serif;
}

.is-relative {
    position: relative;
}

/************************************************************************
************************* GLOBAL FORMS ELEMENTS *************************
/************************************************************************/

.g_obh_form_title {
    font-weight: 400;
    margin: 0 0 15px;
    font-size: 24px;
    display: inline-block;
    width: calc(100% - 100px);
}

.g_obh_form_title_open_extended {
    text-align: right;
    color: #0070d2;
    font-size: 13px;
    text-decoration: none;
    float: right;
    margin-top: 5px;
}

.g_obh_form_title_open_extended:hover {
    text-decoration: underline;
    color: #015ba7;
}

.form-row-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.form-label {
    font-size: 14px;
}

.form-input {
    width: 65%;
}

.form-input input[type=text] {
    background-color: #fff;
    border: 1px solid #d8dde6;
    border-radius: 4px;
    width: calc(100% - 22px);
    padding: 10px;
    font-size: 14px;
    outline: 0;
}

.form-input textarea {
    height: 80px;
    background-color: #fff;
    border: 1px solid #d8dde6;
    border-radius: 4px;
    width: calc(100% - 22px);
    padding: 10px;
    font-size: 14px;
    outline: 0;
    resize: none;
}

.open-in-layout-link {
    text-align: right;
    color: #0070d2;
    font-size: 13px;
    text-decoration: none;
    float: right;
    margin-top: -1px;
    margin-right: 2px;
}

.open-in-layout-link:hover {
    text-decoration: underline;
}