.sut-search input {
    background: #fff;
    border: 1px solid #d8dde6;
    border-radius: 4px;
    width: 260px;
    padding: 8px 11px;
    font-size: 13px;
    outline: 0;
    margin-bottom: 28px;
}

.sut-licenses {
    float: right;
}

.sut-add {
    padding: 1px 10px 2px;
    top: -1px;
    outline: 0;
    margin-left: 5px;
}

.sut-ter-lab-cont {
    width: 162px;
    margin-bottom: 6px;
    display: inline-block;
}

.sut-ter-lab-cont input {
    margin-right: 5px;
    position: relative;
    top: -4px;
}

.sut-ter-lab-cont label {
    width: calc(100% - 25px);
    display: inline-block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

#sut-ter-checkbox {
    border: 1px solid #d8dde6;
    padding: 10px;
    height: 215px;
    overflow-y: overlay;
    border-radius: 3px;
    margin-top: 15px;
    margin-bottom: 13px;
}


.sut-territories-container input[type=search] {
    position: absolute;
    right: 15px;
    top: 18px;
    width: 200px;
    border-radius: 3px;
    border: 1px solid #d8dde6;
    font-size: 13px;
    padding: 5px 10px;
}

.sut-territories-container h1 {
    font-weight: 400;
    font-size: 16px;
    margin-top: 5px;
    display: block;
}

.sut-territories-container h2 {
    font-size: 15px;
    line-height: 24px;
    color: #80858a;
    font-weight: lighter;
    display: block;
    padding-top: 20px;
}

.sut-territories-container h2 a {
    color: #0070d2 !important;
}

#cut-button {
    padding: 4px 10px;
    position: relative;
    right: 0px;
}

#sut-select {
    appearance: none;
    position: relative;
    background: #fff;
    border: 0;
    font-size: 16px;
    margin-left: 5px;
    color: #0071d2;
    border-bottom: 1px dashed #0071d2;
    border-radius: 0;
    outline: 0;
    cursor: pointer;
}

.sut-saving {
    float: left;
}

.sut-saving img {
    width: 24px;
    height: 24px;
    position: relative;
    top: 6px;
    margin-right: 8px;
}

#sharing-users-territORIs .page-header .icon-on-header {
    padding: 6px !important;
    height: 20px !important;
    width: 20px !important;
}

.dispatcher-counter {
    width: 26px;
    height: 26px;
    position: relative;
    top: 7px;
    fill: #fff;
    margin-right: 8px;
    background: #479ac3;
    border-radius: 3px;
}