.rules-select-help {
    border-radius: 50%;
    padding: 1px 0 1px 0px;
    width: 16px;
    display: inline-block;
    text-align: center;
    font-size: 11px;
    margin-left: 8px;
    position: relative;
    top: 5px;
    color: #fff;
    font-style: italic;
    cursor: pointer;
}

.rules-select-help:hover + .rules-select-help-tooltip {
    display: initial !important;
}

.rules-select-help-tooltip {
    display: none;
    position: absolute;
    width: 270px;
    margin-left: -145px;
    padding: 10px;
    border-radius: 3px;
    background: #16325c;
    color: #fff;
    box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.16);
    font-size: 13px;
    line-height: 22px;
    z-index: 100;
    -webkit-font-smoothing: antialiased;
}

.rules-select-help-tooltip::before {
    content: "";
    background: #16325c;
    position: absolute;
    right: calc(50% + 1px);
    z-index: 100;
    padding: 7px;
    border-radius: 3px;
    transform: rotate(45deg);
}

.tooltip-up {
    bottom: 25px;
}

.tooltip-down {
    top: 32px;
}

.tooltip-down::before {
    top: -5px;
}

.tooltip-up::before {
    bottom: -5px;
}

.tooltip-icon {
    width: 18px;
    height: 18px;
    fill: #706e6a;
}

.tooltip-right {
    left: 185px;
    transform: translateY(-50%);
    width: 310px;
}

.tooltip-right:before {
    top: 52%;
    right: 321px;
}