#rd-remaining-licenses {
    float: right;
    font-size: 14px;
}

.rd-remaining {
    display: inline-block;
    margin-left: 10px;
}

#rd-remaining-licenses svg {
    height: 24px;
    width: 24px;
    background: #7dc37d;
    border-radius: 3px;
    fill: #fff;
    margin-right: 6px;
    position: relative;
    top: 6px;
}

.rd-mobile {
    background: #d15d95 !important;
}

.license-tooltip-cont {
    display: inline-block;
    float: right;
    margin-right: 10px;
    margin-top: 3px;
}

.licenses-container {
    height: 30px;
    display: inline;
}