.rd-new-resource-cell {
    display: inline-block;
    width: 21%;
}

.rd-new-resource-cell-small {
    display: inline-block;
    width: 8%;
}

#rd-add-new-resource {
    padding: 6px 15px 15px 15px;
    border: 1px solid #d8dde6;
    background: #fafaf9;
    margin-bottom: 20px;
}

#rd-add-new-resource h1 {
    font-size: 16px;
    padding: 10px 0 0 0;
    display: block;
    font-weight: normal;
    height: 35px;
}

#rd-add-new-resource input[type=text] {
    border: 1px solid #d8dde6;
    border-radius: 4px;
    width: calc(100% - 28px);
    padding: 6px 8px;
    font-size: 13px;
    outline: 0;
}

#rd-add-new-resource select {
    appearance: none;
    padding: 6px 6px;
    z-index: 10;
    position: relative;
    outline: 0;
    font-size: 13px;
    border: 1px solid #d8dde6;
    width: calc(100% - 10px);
    background: #fff;
    border-radius: 4px;
}


#rd-add-new-resource button {
    width: 100%;
    font-size: 13px;
    font-weight: 400;
}


.rd-comments-ul {
    padding-left: 0;
}

.rd-comments-ul li {
    margin-bottom: 10px;
}

#rd-add-new-resource .licenseIcon {
    width: 24px;
    height: 24px;
    position: relative;
    top: 7px;
}

.rd-add-saving-new-res {
    font-size: 14px;
    color: #706e6b;
}

.rd-add-saving-new-res img {
    width: 24px;
    height: 24px;
    position: relative;
    top: 7px;
    margin-right: 5px;
}