.truncate {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.add-new {
    margin-bottom: 15px;
}

.add-new input {
    background-color: #fff;
    border: 1px solid #d8dde6;
    border-radius: 4px;
    width: 180px;
    padding: 7px 8px 8px 8px;
    font-size: 14px;
    margin-right: 5px;
    outline: 0;
}

.add-new button {
    padding: 1px 10px 2px;
    top: -1px;
}

.add-new-disabled,
.add-new-disabled:hover,
.add-new-disabled:active {
    color: #bbc3d2 !important;
    cursor: not-allowed !important;
}
/* FORM */
.side-form {
    position: absolute;
    right: 20px;
    width: calc(50% - 50px);
    padding-left: 20px;
    border-left: 1px solid #d8dde6;
    font-size: 14px;
    min-height: 300px;
    display: inline-block;
    height: calc(100% - 200px);
}

.side-form .select-a {
    font-size: 16px;
    line-height: 30px;
    color: #767676;
    font-weight: lighter;
    padding: 20px 5px;
}

.side-form .form-row-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.side-form .form-label {
    width: 35%;
}

.side-form .form-input {
    width: 65%;
}

.side-form .form-input select {
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    padding: 10px;
    background: transparent;
    border: 0;
    width: 100%;
    z-index: 10;
    position: relative;
    outline: 0;
    font-size: 14px;
}

.side-form .form-input input[type="text"],
.side-form .form-input input[type="number"] {
    background-color: #fff;
    border: 1px solid #d8dde6;
    border-radius: 4px;
    width: calc(100% - 22px);
    padding: 10px;
    font-size: 14px;
}

.side-form .form-input select {
    border: 1px solid #d8dde6;
    border-radius: 4px;
    padding: 10px;
    font-size: 14px;
}

.side-form.form-arrow::before {
    content: "";
    background: #fff;
    position: absolute;
    margin-left: -31px;
    margin-top: 30px;
    width: 20px;
    height: 20px;
    transform: rotate(45deg);
    border-left: 1px solid #d8dde6;
    border-bottom: 1px solid #d8dde6;
}

.side-form .form-select::before {
    content: '';
    position: absolute;
    background: transparent;
    padding: 3px;
    border-bottom: 1px solid #c3c8d0;
    border-right: 1px solid #c3c8d0;
    transform: rotate(45deg);
    right: 14px;
    margin-top: 13px;
}

/*global guided-setup button */

.guided-setup-button {
    position: relative;
    display: inline-block;
    padding: 0;
    background: transparent;
    background-clip: border-box;
    border: 1px solid transparent;
    border-radius: .25rem;
    line-height: 1.875rem;
    text-decoration: none;
    color: #0070d2;
    -webkit-appearance: none;
    white-space: normal;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    cursor: pointer;
    font-weight: 400;
    font-size: 14px;
}

.guided-setup-button:active {
    color: #005fb2;
}

.guided-setup-button_brand {
    padding-left: 1rem;
    padding-right: 1rem;
    text-align: center;
    vertical-align: middle;
    border: 1px solid #dddbda;
    -webkit-transition: border 0.15s linear;
    transition: border 0.15s linear;
    background-color: #0070d2;
    border-color: #0070d2;
    color: #fff;
}

.guided-setup-button_brand:active {
    background-color: #005fb2;
    border-color: #005fb2;
}

.guided-setup-button_brand:hover, .guided-setup-button_brand:focus {
    background-color: #005fb2;
    border-color: #005fb2;
    color: #fff;
}

.guided-setup-button_neutral {
    padding-left: 1rem;
    padding-right: 1rem;
    text-align: center;
    vertical-align: middle;
    border: 1px solid #dddbda;
    -webkit-transition: border 0.15s linear;
    transition: border 0.15s linear;
    border-color: #dddbda;
    background-color: #fff;
}

.guided-setup-button_neutral:active {
    background-color: #eef1f6;
}

.guided-setup-button_neutral:hover, .guided-setup-button_neutral:focus {
    background-color: #f4f6f9;
}

.guided-setup-button_text-destructive {
    padding-left: 1rem;
    padding-right: 1rem;
    text-align: center;
    vertical-align: middle;
    border: 1px solid #dddbda;
    transition: border .15s linear;
    border-color: #dddbda;
    background-color: #fff;
    color: #c23934;
}

.guided-setup-button_text-destructive:active {
    background-color: #eef1f6;
}

.guided-setup-button_text-destructive:focus, .guided-setup-button_text-destructive:hover {
    color: #a12b2b;
}