#rd-the-table {
    margin-top: 24px;
    height: calc(100% - 313px);
}


.rd-user-table {
    height: 100%;
}

.rd-table-head {
    font-size: 0.75rem;
    line-height: 1.25;
    color: #706e6b;
    text-transform: uppercase;
    letter-spacing: 0.0625rem;
    box-shadow: 0 -1px 0 #dddbda;
    background: #fafaf9;
    border-bottom: 1px solid #dddbda;
    padding: 10px;
}


.rd-table-cell span svg {
    width: 13px;
    height: 13px;
    float: right;
    margin: 3px 10px 0 0;
    fill: #8e8e8e;
    cursor: pointer;
    display: none;
}

.rd-table-cell span svg:hover {
    fill: #0071d2;
}



.rd-table-row:hover span svg {
    display: initial !important;
}

.rd-table-row:hover {
    background: #f4f6f9;
}

.rd-table-selected {
    background: #fafbfd;
    box-shadow: inset 3px 0 0 #0070d2;
}


.rd-table-head-cell {
    display: inline-block;
    width: 21%;
    user-select: none;
    cursor: pointer;
}

.rd-table-head-user {
    width: calc(21% - 25px);
}

.rd-table-head-selected {
    width: 25px;
}

.rd-table-head-cell svg {
    width: 12px;
    height: 12px;
    fill: #706e6b;
    position: absolute;
    margin: 2px 0 0 2px;
}



.rd-table-head-cell-small {
    width: 8%;
}

.rd-table-body {
    overflow-y: overlay;
    height: calc(100% - 57px);
}

.rd-table-row {
    padding: 10px;
    border-bottom: 1px solid #dddbda;
}

.rd-table-cell {
    display: inline-block;
    width: 21%;
}

.rd-table-cell-selected {
    width: 25px;
}

.rd-table-cell-user {
    width: calc(21% - 25px);
}

.rd-table-cell-small {
    width: 8%;
}

.rd-resource-licenses span svg {
    display: initial;
    float: initial;
    margin: initial;
}